.news_page .news_page_content {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

.news_page .news_page_content h2 {
  font-size: 30px;
  font-family: 'RobotoBold', sans-serif;
  text-align: center;
}

.news_page .news_page_content .news_page_items {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(66.66666666666667% - 10px);
}